import { Controller, Block } from '@ali/4ever-cangjie';
import getClosestDom from './getClosestDom';
import { DROP_POSITION } from '../types';

export default function getDropHolderDataFromViewKeys(
  controller: Controller,
  event: DragEvent,
  viewKeys: string[],
) {
  const { document } = controller.value;
  const startViewKey = viewKeys[0];
  const endViewKey = viewKeys[viewKeys.length - 1];
  if (startViewKey === endViewKey) return undefined;

  const startViewNode = document.getNode(startViewKey) as Block;
  const endViewNode = document.getNode(endViewKey) as Block;
  const startNode = getClosestDom(startViewNode);
  const endNode = getClosestDom(endViewNode);
  if (!startNode || !endNode) return undefined;

  // 处理跨页的情形
  const startNodeRect = startNode.getBoundingClientRect();
  const endNodeRect = endNode.getBoundingClientRect();
  const { clientY } = event;

  if (clientY > startNodeRect.top && clientY < startNodeRect.top + startNodeRect.height) {
  // 在前
    return {
      rect: {
        top: startNodeRect.top,
        left: startNodeRect.left,
        width: startNodeRect.width,
        height: startNodeRect.height,
      },
      pagePosition: DROP_POSITION.before,
    };
  } else {
  // 在后
    return {
      rect: {
        top: endNodeRect.top,
        left: endNodeRect.left,
        width: endNodeRect.width,
        height: endNodeRect.height,
      },
      pagePosition: DROP_POSITION.after,
    };
  }
}